import styled, { css } from "styled-components";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { Title1, Title2 } from "../../assets/styles/component";

export const SIntro = styled.section`
  margin-top: 75px;
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  overflow: hidden;

  // .SIntro > p

  & > p {
    margin-bottom: 50px;
    text-align: center;
  }
`;

export const Title = styled(Title1)`
  text-align: center;
  width: fit-content;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  margin-bottom: 25px;
  font-size: 28px;

  span:nth-child(2) {
    align-self: flex-end;
  }

  span:nth-child(3) {
    align-self: flex-start;
  }
`;

export const BlueBG = styled.span`
  background: radial-gradient(
      81.57% 98.97% at -3.3% -14.6%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #0202f1;
  box-shadow: inset 0px 0px 43px rgba(255, 255, 255, 0.25);
  border-radius: 63.5px;
  padding: 4px 25px 6px 25px;
  align-self: flex-start;
  font-size: 28px;
`;

export const PinkBG = styled.span`
  position: relative;
  padding: 0px 35px 10px 25px;
  margin: 0 0 0 30px;
  border-radius: 103px;
  border-top-right-radius: 0;

  background: radial-gradient(
      123.91% 117.38% at 111.7% 118.95%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #cf2e82;
  box-shadow: inset 0px 0px 43px rgba(255, 255, 255, 0.25);

  svg {
    position: absolute;
    width: 50px;
    top: -35px;
    right: 0;
    left: unset;
    transform: rotateY(180deg);
  }
`;

export const FrontImg = styled.img`
  z-index: 1;
  width: 100%;
  height: 100%;
`;

export const BackImg = styled.img`
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
`;

export const FrontImgLazy = styled(LazyLoadImage)`
  z-index: 1;
  width: 100%;
  height: 100%;
`;

export const BackImgLazy = styled(LazyLoadImage)`
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
`;

const bg = css`
  content: "";;
  z-index: -1;
  position: absolute;
`;

export const FrameBoxContainer = styled.div`
  position: relative;
  overflow: visible;
  &::after {
    content: "";
    position: absolute;
    top: -30%;
    left: -70%;
    width: 230%;
    height: 145%;
    z-index: -1;
    background: radial-gradient(
        25.27% 36.6% at 50% 50%,
        rgba(241, 2, 117, 0.81) 0%,
        rgba(241, 2, 117, 0) 100%
      ),
      radial-gradient(
        24.63% 36.71% at 37.85% 36.14%,
        #0202f1 0%,
        rgba(2, 2, 241, 0) 100%
      ),
      radial-gradient(
        19.47% 28.35% at 55.07% 60.96%,
        #ff429e 0%,
        rgba(255, 66, 158, 0) 100%
      ),
      radial-gradient(
        21.4% 32.49% at 68.89% 47.03%,
        #cb1d85 0%,
        rgba(203, 29, 133, 0) 100%
      );

    backdrop-filter: blur(56.5px);
  }
`;

export const FrameBox = styled.div`
  position: relative;
  margin-bottom: 80px;
  width: 85vw;
  max-width: 1000px;

  // .FrameBox:nth-child(3)

  &:nth-child(3) {
    & > div:first-child {
      transform: translate(-30%, -30%) rotate(-14deg) scale(0.4);
      left: 0;
      top: 0;

      @media (min-width: 450px) {
        transform: translate(-30%, -30%) rotate(-14deg) scale(0.6);
      }
    }

    // .promo:last-child

    & > div:last-child {
      bottom: 20%;
      right: -20%;

      transform: translate(19%) rotate(14deg) scale(0.3);

      @media (min-width: 450px) {
        transform: rotate(14deg) scale(0.5);
      }
    }

    & > div:nth-child(2) {
      // .Frame:nth-child(2) > img:first-child
      & > img:first-child {
        transition: 0.5s ease-in-out;
        ${({ show = false }) => show && ` opacity: 0; `}
      }
    }
  }

  // .FrameBox:nth-child(6)

  &:nth-child(6) {
    margin: 60px 0 130px 0;

    // .FrameBox:nth-child(6) > .BlurCard:first-child

    & > div:first-child {
      transform: translate(-15%, 25%) rotate(-12.9deg) scale(0.4);

      @media (min-width: 450px) {
        transform: translate(3vw, 10%) rotate(-12.9deg) scale(0.5);
      }
    }

    // .FrameBox:nth-child(6) > .promo:last-child

    & > div:last-child {
      bottom: 5%;
      right: 2%;
      transform: translate(22%) rotate(14deg) scale(0.5);
    }

    // .FrameBox:nth-child(6) > .Frame:nth-child(6) > img:first-child

    & > div:nth-child(6) {
      & > img:first-child {
        transition: 0.5s ease-in-out;
        ${({ show2 = false }) => show2 && ` opacity: 0; `}
      }
    }
  }
`;

export const Frame = styled.div`
  position: relative;
  max-width: 999px;
  margin: 0 auto;
  border-radius: 14px;
  outline: 5px solid #ffffff22;

  background: radial-gradient(
    240.7% 80.8% at 107.77% 65.48%,
    #a927ca 0%,
    #d72f75 41.67%,
    #e79079 76.04%,
    #e3592f 98.44%
  );

  // .Frame > img

  & > img {
    width: 100%;
    border-radius: 14px;
    margin-bottom: -4px;
  }
`;

export const Promo = styled.div`
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding: 15px 20px;
  border-radius: 32px;
  background: rgba(217, 217, 217, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.19);
  backdrop-filter: blur(23px);
  z-index: 5;

  right: 0;
  bottom: 0;

  // .Promo small

  small {
    font-size: 12px;
    color: #ffffff;
    opacity: 0.43;
  }

  // .Promo img

  img {
    transition: 0.25s ease-in-out;
    cursor: pointer;

    // .Promo img:hover

    &:hover {
      transform: scale(1.02);
    }
  }
`;

export const BlurCard = styled.div`
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding: 15px 20px;
  border-radius: 25px;
  background: rgba(217, 217, 217, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.19);
  backdrop-filter: blur(23px);
  z-index: 5;
  height: fit-content;

  left: -17%;
  bottom: 0%;

  // .BlurCard small

  small {
    font-size: 12px;
    color: #ffffff;
    opacity: 0.43;
  }
`;

export const SubTitle = styled(Title2)`
  margin-bottom: 31px;

  font-size: 26px;
`;

export const PlayBtn = styled(LazyLoadImage)`
  position: absolute;
  bottom: 10%;
  left: -10%;
  width: 15vw;
  max-width: 190px;
  min-width: 53px;
  transform: rotate(-19deg);
`;

export const FrameText = styled.div`
  position: absolute;
  background: #ffffff;
  border-radius: 10px;
  transform: rotate(-8.98deg);
  padding: 25px 10px 10px 10px;
  height: fit-content;
  max-width: 130px;
  z-index: 2;
  box-sizing: border-box;
  bottom: -65%;
  left: 5%;

  @media (min-width: 400px) {
    bottom: -45%;
    transform: scale(1.2) rotate(-8.98deg)  ;
  }

  // .FrameText img

  img {
    position: absolute;
    top: -45%;
    left: -5%;
    width: 70px;
  }

  // .FrameText p

  p {
    letter-spacing: 0.01em;
    color: #0f0f0f;
    font-size: 8px;
    line-height: 120%;
  }
`;

export const Tag = styled.div`
  position: absolute;
  z-index: 2;
  padding: 10px 29px 9px 18px;
  border-radius: 50px;
  box-shadow: inset 0px 0px 25.3184px rgba(255, 255, 255, 0.25);
  color: #fff;

  // .Tag:nth-child(4)

  &:nth-child(4) {
    border-top-right-radius: 0;
    bottom: -30px;
    right: 5%;
    background: #ffa011;
    transform: scale(0.7) rotate(10deg);

    // .Tag:nth-child(4) svg

    svg {
      position: absolute;
      top: 0;
      right: 0;
      transform: translateY(-100%);
    }
  }

  // .Tag:nth-child(3)

  &:nth-child(3) {
    border-bottom-right-radius: 0;
    top: -23%;
    right: 0;
    background: #4354f1;
    transform: translateX(20%) rotate(10.72deg) scale(0.5);

    @media (min-width: 400px) {
      transform: translateX(10%) rotate(10.72deg) scale(0.7);
      top: -15%;
    }

    // .Tag:nth-child(3) svg

    svg {
      position: absolute;
      bottom: -98%;
      right: 0;
      transform: translateY(-100%) rotate(180deg);
    }
  }
`;

export const Arrow = styled(LazyLoadImage)`
  position: absolute;
  bottom: -15%;
  left: 3%;
  width: 6vw;
  z-index: 10;

  @media (min-width: 400px) {
    bottom: -10%;
    width: 5vw;
  }
`;
